* {
  box-sizing: border-box;
}

.grid {
  width: 100vw;
  height: 100vh;
  display: block;
  padding: 0;
}
.grid.wide {
  display: flex;
  align-items: center;
  justify-content: center;
}

.grid-child {
  width: 100%;
  display: block;
  padding: 0;
}

.grid-child.wide-child {
  max-width: 1630px;
  margin: 0 auto;
}

.grid-sidebar {
  width: 100%;
  height: 100%;
  display: block;
  padding: 0;
}

.grid-sidebar.wide-sidebar {
  max-width: 100%;
  max-height: 100%;
}

.row {
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
}

.row-1 {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.row-2 {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
}

.col {
  width: 100%;
  height: 100%;
}

.row-sidebar {
  width: 100%;
  height: 100%;
  max-height: calc(100% - 132px);
  min-height: 89px;
  margin: 0 0 12px 0;
}

.row-sb {
  width: 100%;
}

//Large
@media (min-width: 1600px) {
  .l-0 {
    display: none;
  }
  .l-1,
  .l-2,
  .l-2-5,
  .l-3,
  .l-4,
  .l-5,
  .l-6,
  .l-7,
  .l-8,
  .l-9,
  .l-10,
  .l-11,
  .l-12 .l-12-sb {
    display: block;
  }
  .l-1 {
    max-width: 8.33333%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 8.33333%;
  }
  .l-1-8 {
    max-width: 12.5%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 12.5%;
  }

  .l-2 {
    max-width: 16.66667%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 16.66667%;
  }

  .l-2-5 {
    max-width: 20%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 20%;
  }

  .l-3 {
    max-width: 25%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 25%;
  }
  .l-4 {
    max-width: 33.33333%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 33.33333%;
  }
  .l-5 {
    max-width: 41.66667%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 41.6667%;
  }
  .l-6 {
    max-width: 50%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 50%;
  }
  .l-7 {
    max-width: 58.33333%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 58.33333%;
  }
  .l-8 {
    max-width: 66.66667%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 66.6667%;
  }
  .l-9 {
    max-width: 75%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 75%;
  }
  .l-10 {
    max-width: 83.33333%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 83.33333%;
  }
  .l-11 {
    max-width: 91.66667%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 91.6667%;
  }
  .l-12 {
    max-width: 100%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 100%;
  }

  .l-12-sb {
    max-width: 100%;
    height: 100%;
  }
}

