.supports {
  width: 302px;
  padding: 4.43px 24px;
  background-image: var(--child-background-color);
  border: var(--main-border);
  border-radius: var(--main-border-radius);

  display: flex;
  align-items: center;
}

.helps {
  font-size: 1rem;
}

.title {
  margin: 12px 0;
}

.languages {
  width: 130px;
  height: 40px;
  margin-left: auto;
  border: 1px solid rgba(255, 255, 255, 0.8);
  border-radius: var(--main-border-radius);

  display: flex;
  align-items: center;
  justify-content: center;
}

.icon {
  width: 22px;
  height: 22px;
  color: var(--main-move-color);
}

.choose {
  width: 90px;
  font-size: 0.6rem;
}

.change-lang {
  width: 128px;
  height: 100%;
  border-radius: 7px;
  border: 1px solid gray;
  background-color: rgba(12, 12, 20, 1);
  display: flex;
  flex-direction: column;
  transform: translateY(11px);
}

.lang {
  height: 30px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.8);
}

.lang:hover {
  background-color: rgba(128, 128, 128, 0.5);
}

.choose-lang {
  font-size: 0.6rem;
  color: rgba(255, 255, 255, 0.7);
}
